.login .chooseLanguage {
    height: 150px;
}
.login .chooseLanguage .languageList {
      position: absolute;
      top: 68px;
      right: 30px;
      width: auto;
}
.login .chooseLanguage .languageList .el-tabs__item {
        color: #fff;
}
.login .chooseLanguage .languageList :hover {
        color: #E26829 !important;
}
.login .main-container {
    height: calc(100% - 300px);
    position: relative;
    /*.language-dialog {*/
    /*top: 50px;*/
    /*right: 0;*/
    /*transform: translateX(0%);*/
    /*left: initial;*/
    /*}*/
}
.login .main-container .main-warp {
      overflow: auto;
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      text-align: center;
      font-size: 0;
}
.login .main-container .main-warp .head {
        font-size: 0;
        margin: 0 auto;
        border-radius: 50%;
        width: 210px;
        height: 210px;
        background: white;
        overflow: hidden;
}
.login .main-container .main-warp .head .icon-login {
          width: 210px;
          height: 210px;
}
.login .main-container .main-warp .head .icon-login .logo-img {
            position: relative;
            top: 105px;
            -webkit-transform: translate(0, -50%);
                    transform: translate(0, -50%);
            width: 100%;
            height: auto;
}
.login .main-container .main-warp .login-text {
        font-size: 18px;
        margin-top: 20px;
        margin-bottom: 32px;
        line-height: 24px;
}
.login .main-container .main-warp .username,
      .login .main-container .main-warp .password {
        width: 240px;
        font-size: 12px;
        display: block;
        left: 50%;
        margin-left: -120px;
}
.login .main-container .main-warp .username > input,
        .login .main-container .main-warp .password > input {
          text-align: center;
          height: 40px;
}
.login .main-container .main-warp .username > input {
        border-radius: 15px 15px 0 0;
        font-size: 14px;
}
.login .main-container .main-warp .password {
        margin-top: 1px;
}
.login .main-container .main-warp .password > input {
          border-radius: 0 0 15px 15px;
          font-size: 14px;
}
.login .main-container .main-warp .check-div {
        overflow: hidden;
        margin-top: 24px;
        font-size: 0;
}
.login .main-container .main-warp .check-div .remember-text {
          float: left;
          margin-left: 9px;
          font-size: 12px;
          line-height: 14px;
}
.login .main-container .main-warp .check-div .forget {
          color: #858890;
          margin-left: 30px;
          float: right;
          text-decoration: underline;
          font-size: 12px;
          cursor: pointer;
          line-height: 14px;
}
.login .main-container .main-warp .check-div .forget:hover {
            color: #00ffff;
}
.login .main-container .main-warp .check-div .check-item {
          float: left;
          background: #2b2f3b;
          width: 16px;
          height: 16px;
          border-radius: 50%;
}
.login .main-container .main-warp .check-div .check-item .icon-check {
            width: 8px;
            height: 8px;
            background: #e26829;
            border-radius: 50%;
            position: relative;
            left: 4px;
            top: 4px;
}
.login .main-container .main-warp .login-btn {
        height: 29px;
        width: 240px;
        margin-top: 27px;
}
.login .main-container .main-warp .login-btn span {
          font-size: 16px;
}
.login .main-container .main-warp .sign {
        height: 29px;
        width: 240px;
}
.login .main-container .main-warp .sign span {
          font-size: 16px;
}
.login .main-container .declare {
      position: absolute;
      left: 90px;
      top: -50px;
      width: 300px;
      text-align: left;
}
.login .main-container .declare h2 {
        color: #fff;
        border-bottom: 1px solid #fff;
        padding-bottom: 0.8em;
        font-size: 180%;
        font-weight: 400;
}
.login .main-container .declare p {
        padding-top: 15px;
        line-height: 1.5;
        letter-spacing: .8px;
}
.login .main-container .declare bodytext {
        font-size: 12px;
}
.login .main-container .declare .showMore {
        margin-top: 10px;
        padding: 6px 9px;
}
.login .main-container .declare .showMore .el-icon-arrow-right {
          font-size: 12px;
          margin-right: -5px;
}
.login .dialogInfo {
    width: 100%;
    background: #2B2F3B;
    padding-right: 0;
    top: 0 !important;
}
.login .dialog {
    position: absolute;
    top: 111px;
    left: 60px;
    width: 94%;
    height: 620px;
    overflow: hidden;
}
.login .dialog .flex {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -ms-flex-wrap: nowrap;
          flex-wrap: nowrap;
}
.login .dialog .flex .item {
        width: 33.3%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        background: #666;
        -webkit-box-orient: vertical;
        -webkit-box-direction: normal;
            -ms-flex-direction: column;
                flex-direction: column;
        -ms-flex-line-pack: start;
            align-content: flex-start;
}
.login .dialog .flex .item h2 {
          margin: 0;
          padding: 0;
          padding-left: 22px;
          padding-bottom: 0.8em;
          width: 100%;
          font-size: 22px;
          color: #fff;
          background: #2B2F3B;
}
.login .dialog .flex .last {
        width: 33.4% !important;
}
.login .dialog .el-dialog__header {
      height: 23px;
      padding: 0;
      padding-right: 20px;
}
.login .dialog .el-dialog__headerbtn {
      -webkit-transform: translateY(18px);
              transform: translateY(18px);
}
.login .dialog .el-dialog__body {
      padding: 0;
      background: #fff;
}
.login .dialog .el-dialog__body .overflow {
        padding: 8px;
        padding-bottom: 15px;
        margin-right: 1px;
        background: #fff;
}
.login .dialog .el-dialog__body .overflow .declareInfo {
          padding: 10px 30px 15px 15px;
          min-height: 520px;
          border: 1px solid #eaeaea;
          height: 520px;
          overflow: auto;
          color: #666;
          background: #fff;
          letter-spacing: .5px;
}
.login .dialog .el-dialog__body .overflow .declareInfo h3 {
            margin-bottom: 5px;
            margin-top: 2px;
}
.login .dialog .el-dialog__body .overflow .declareInfo p {
            font-size: 14px;
}
.login .dialog .el-dialog__body .overflow .declareInfo p span {
              padding-left: 2em;
}
.login .dialog .el-dialog__body .overflow .declareInfo .special p {
            display: inline-block;
            width: 84.5%;
            font-size: 13px;
            font-weight: 600;
}
.login .dialog .el-dialog__body .overflow .declareInfo .special img {
            display: inline-block;
            width: 15%;
}



