@import "./style.scss";
@import "./util.scss";
@import "./custom.scss";


.el-loading-mask {
    background-color: transparent;
}

.el-dropdown-menu {
    background-color: #98b42c;
    border: 1px solid #98b42c;
    box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .12);
}

.el-dropdown-menu__item--divided:before {
    background-color: #98b42c;
}


.el-dropdown-menu__item:not(.is-disabled):hover {
    background-color: rgb(241, 236, 228);
    color: #98b42c;
}

.el-date-editor.el-input {
    width: 180px;
}

.search-header {
    position: relative;
    padding: 0 40px;
    border-bottom: 1px solid #2B2F3B;
    background: #383d49;

    .list-form-inline {
        height: 100%;
        line-height: 50px;
        display: inline-block;
        width: calc(100% - 46px);
    }

    .btn-group-inline {
        display: inline-block;
        vertical-align: top;
        height: 100%;
        padding-top: 3px;
        min-height: 59px;
    }

    .btn-group-inline-icon {
        display: inline-block;
        vertical-align: middle;
    }

    .btn-group-top {
        position: absolute;
        height: 40px;
        right: 10px;
        top: 10px;
        width: auto;
    }
}

// 货币选择框
.el-select .el-input.is-disabled .el-input__inner:hover {
    border-color: transparent;
}

// 表格
.table-wrap {
    height: calc(100% - 51px);
}

//进度管理
.el-step__head.is-text.is-process{
    color: #fff;
    background-color: #E26829;
    border-color: #E26829;
}

.el-step__head.is-text.is-wait{
    color: #fff;
    background-color: #858890;
    border-color: #858890;
}


