.header {
    position: relative;
    min-height: 60px;
    line-height: 60px;
    padding: 0 40px;
    border-bottom: 1px solid #2B2F3B;
    background: #383d49;
    .title {
        font-size: 14px;
        color: #fff;
        margin-right: 15px;
        vertical-align: middle;
    }
    button+.title {
        margin-left: 15px;
    }
    .el-button+.el-button {
        margin-left: 15px;
    }
    .btn-group {
        position: absolute;
        height: 30px;
        right: 40px;
        top: 0;
        width: auto;
    }
    .btn-group-top {
        position: absolute;
        height: 40px;
        right: 40px;
        top: 10px;
        width: auto;
    }
    .btn-group-left {
        position: absolute;
        height: 40px;
        left: 40px;
        top: 10px;
        width: auto;
    }
    .btn-group-inline {
        display: inline-block;
        vertical-align: middle;
    }
}

.content-wrap {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    margin-top: 60px;
    padding: 20px 0;
    overflow-y: auto;
    .content {
        width: 950px;
        margin: 0 auto;
    }
}

// 表格
.table-wrap {
    width: 100%;
    height: calc(100% - 60px);
    overflow: auto;
    .table-content-holder {
        overflow: auto;
        width:100%;
        height: calc(100% - 60px);
    }
    .table-content {
        width: 100%;
        overflow: auto;
        //padding-bottom: 60px;
        thead td {
            height: 50px;
            padding: 10px 20px;
            text-align: left;
            white-space: nowrap;
            font-size: 14px;
            color: #A1A5B1;
        }
        thead td.al-right {
            text-align: right;
        }
        tr {
            height: 50px;
            &:nth-child(2n + 1) {
                background: rgba(39, 43, 55, 0.20);
                box-shadow: 1px 0 0 1px #2B2F3B;
            }
            td {
                padding: 0px 20px;
                text-align: left;
                white-space: nowrap;
                font-size: 14px;
                img {
                    margin-top: 6px;
                    background-color: white;
                    border-radius: 4px;
                    width: 38px;
                    height: 38px;
                }
                &.desc {
                    opacity: 0.5;
                }
            }
            td.al-right {
                text-align: right;
            }
        }
        tr.large {
            height: 80px;
            td img {
                margin-top: 0px;
                height: 50px;
                width: 50px;
            }
        }
        tbody tr {
            cursor: pointer;
        }
        tbody tr .no-pointer {
            cursor: default;
        }
    }
    .table-footer {
        z-index: 100;
        position: absolute;
        bottom: 0;
        width: 100%;
        height: 60px;
        padding: 15px 40px;
        background-color: #383d49;
        box-shadow: 1px 0 0 1px #2B2F3B;
        .total {
            position: relative;
            top: 5px;
            display: inline-block;
            padding-left: 5px;
            font-size: 14px;
            color: rgba(255, 255, 255, 0.5);
            font-weight: bold;
        }
        .el-pagination {
            display: inline-block;
        }
    }
    .no-data-content {
        //position: absolute;
        //bottom: 0;
        //top:0;
        width: 100%;
        //height:100%;
        height: calc(100% - 50px);
        //height: 120px;
        padding-top: 50px;
        font-size: 14px;
        background: #2B2F3B;
        text-align: center;
        color: #A1A5B1;
    }
    .loading {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

