
.container {
  overflow: hidden;
  height: 100%;
}
.container .main {
    height: calc(100% - 50px);
    width: 100%;
}
.container .dialog-setting .item {
    height: 30px;
    margin: 0 20px;
}
.container .dialog-setting .item .label {
      display: inline-block;
      width: 150px;
}
.container .dialog-setting .item .item-a {
      color: #FFFFFF;
      text-decoration: underline;
}
.container .dialog-setting .item .item-a:hover {
        color: #E26829;
}
.container .dialog-setting .item .select {
      width: 242px;
}
.container .dialog-setting .version {
    margin: 26px 20px;
}
.container .dialog-setting .version .label {
      display: inline-block;
      width: 80px;
}
.container .dialog-setting .version .version-label {
      margin-left: 70px;
      color: #ffffff;
      display: inline;
}
.container .dialog-setting .version .update-btn {
      margin-left: 10px;
      display: inline;
      background-color: #98B42C;
}
.container .dialog-password .item {
    font-size: 0;
    height: 30px;
}
.container .dialog-password .item .item-box {
      line-height: 30px;
}
.container .dialog-password .item .label {
      display: inline-block;
      width: 120px;
      height: 30px;
      vertical-align: middle;
}
.container .dialog-password .item .input {
      margin-left: 40px;
      width: 260px;
}
.container .dialog-password .new-password {
    margin: 20px 0;
}
.container .dialog-auth .el-dialog__body {
    text-align: center;
}
.container .dialog-company {
    overflow: auto;
}
.container .dialog-company .el-dialog__body {
      font-size: 0;
}
.container .dialog-company .company-item {
      width: 50%;
      line-height: 30px;
      display: inline-block;
}
.container .dialog-company .company-item .land {
        width: 250px;
}
.container .dialog-company .company-item .title {
        display: inline-block;
        width: 80px;
}
.container .dialog-company .company-item .logo {
        margin-top: 10px;
        vertical-align: top;
}
.container .dialog-company .company-item .content {
        margin: 10px;
        display: inline-block;
        width: 250px;
}
.container .dialog-company .company-item .content .upload-icon {
          max-width: 142px;
          max-height: 142px;
}
.container .dialog-company .detail-item {
      width: 50%;
      line-height: 30px;
      display: inline-block;
}
.container .dialog-company .detail-item .title {
        display: inline-block;
        width: 120px;
}
.container .dialog-company .detail-item .logo {
        margin-top: 10px;
        vertical-align: top;
}
.container .dialog-company .detail-item .content {
        margin: 10px;
        display: inline-block;
        color: #fff;
}
.container .dialog-company .detail-item.img-item {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
}
.container .dialog-company .detail-item.img-item .content {
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          width: 144px;
          height: 144px;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
          -webkit-box-pack: center;
              -ms-flex-pack: center;
                  justify-content: center;
          border: 1px solid #fff;
}
.container .dialog-company .detail-item.img-item .content .upload-icon {
            max-width: 142px;
            max-height: 142px;
}
.container .history-table {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.1);
}
.container .history-table tr {
      line-height: 30px;
}
.container .history-table .head {
      color: white;
}
.container .history-table .history-td {
      padding: 0 6px;
      width: 300px;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      font-size: 14px;
      text-align: center;
}
.container .history-table .img {
      width: auto;
      height: 30px;
      vertical-align: middle;
      background-color: white;
}



