
@charset "UTF-8";
.controller {
  width: 100%;
  height: 100%;
  overflow: auto;
  padding: 44px 0;
}
.controller .btn-back {
    margin-left: 40px;
    top: -26px;
    position: relative;
}
.controller .hmi-content {
    width: 950px;
    margin: 0 auto;
}
.controller .hmi-content .connect-tip {
      display: inline-block;
      float: right;
}
.controller .hmi-content .connect-tip a {
        color: #E26829;
}
.controller .hmi-content .connect-tip .tip-img {
        top: 0;
        right: 20px;
}
.controller .hmi-content .title {
      margin-left: 33px;
      font-size: 20px;
      /*display: flex;*/
      -webkit-box-align: center;
          -ms-flex-align: center;
              align-items: center;
}
.controller .hmi-content .title .mark {
        display: inline-block;
        width: 4px;
        height: 20px;
        margin-right: 4px;
        background-color: #E4662A;
}
.controller .hmi-content .info {
      overflow: hidden;
      background: #353945;
      width: 950px;
      /*min-height: 380px;*/
      margin-top: 22px;
      /* 老的 Firefox */
      -webkit-box-shadow: 0 1px 0 #2B2F3B;
              box-shadow: 0 1px 0 #2B2F3B;
}
.controller .hmi-content .info .btn-row {
        height: 30px;
        width: 100%;
        position: relative;
        line-height: 30px;
}
.controller .hmi-content .info .border-bottom {
        border-bottom: 1px solid #2B2F3B;
}
.controller .hmi-content .info .info-left {
        float: left;
        margin-top: 9px;
        width: 474px;
        border-right: 1px dotted #2B2F3B;
}
.controller .hmi-content .info .info-left .item {
          margin-left: 33px;
          height: 50px;
          line-height: 50px;
}
.controller .hmi-content .info .info-left .item .el-select {
            width: 187px;
}
.controller .hmi-content .info .info-left .item .tip {
            display: inline-block;
            border-radius: 50%;
            width: 10px;
            height: 10px;
            background: #E26829;
            margin: 0 6px;
}
.controller .hmi-content .info .info-left .item .connect {
            background: #76EE00;
}
.controller .hmi-content .info .info-left .item .label {
            width: 110px;
            display: inline-block;
            line-height: 20px;
            vertical-align: middle;
            word-wrap: break-word;
}
.controller .hmi-content .info .info-left .item .input {
            width: 295px;
}
.controller .hmi-content .info .info-left .item .input > input {
              height: 29px;
}
.controller .hmi-content .info .info-left .item .input-sn:after {
            top: 15px;
}
.controller .hmi-content .info .info-left .item .el-select {
            width: 290px;
}
.controller .hmi-content .info .info-left .btn-group {
          position: absolute;
          right: 40px;
}
.controller .hmi-content .info .info-right {
        float: left;
        margin-top: 9px;
        width: 474px;
}
.controller .hmi-content .info .info-right .item {
          margin-left: 33px;
          height: 50px;
          line-height: 50px;
          display: -webkit-box;
          display: -ms-flexbox;
          display: flex;
          -webkit-box-align: center;
              -ms-flex-align: center;
                  align-items: center;
}
.controller .hmi-content .info .info-right .input {
          width: 295px;
}
.controller .hmi-content .info .info-right > button {
          margin-right: 12px;
}
.controller .hmi-content .info .info-right .label {
          width: 110px;
          display: inline-block;
          line-height: 20px;
          vertical-align: middle;
          word-wrap: break-word;
}
.controller .hmi-content .info .info-right .btn-group {
          position: absolute;
          right: 26px;
}
.controller .hmi-content .info .info-right .btns {
          display: inline-block;
}
.controller .hmi-content .info .info-right .loading {
          display: inline-block;
          vertical-align: middle;
}
.controller .hmi-content .info .btns-right {
        position: relative;
        float: right;
        margin-right: 30px;
}
.controller .hmi-content .info .note-title {
        margin-top: 10px;
}
.controller .hmi-content .info .load-btn {
        margin-left: 33px;
}
.controller .hmi-content .info .info-group .table-wrap {
        width: 880px;
        margin-left: 33px;
        border-top: 1px dotted #2B2F3B;
        color: white;
}
.controller .hmi-content .info .info-group .btn {
        margin-left: 33px;
}



